

















import Vue from 'vue';
import { fitur_for } from '@/service/fiturs';
import FRow from './FRow.vue';


export default Vue.extend({
  name: 'Fitur',

  components: {
    FRow
  },

  data: () => ({
    vocals: [] as any[],
    consonants: [] as any[]
  }),

  methods: {

    async loadData() {
      this.vocals = await fitur_for('vocal');
      this.consonants = await fitur_for('konsonan');
    }

  },

  mounted() {
    this.loadData();
  }

});
